<template>
  <div>
      <img src="static/warn.png" class="warnImgClass">
      <h4>{{msg}}</h4>
      <button class="register" @click="registerClcik">立即注册</button>
  </div>
</template>

<script>
export default {
  name: 'PromptRegistration',
  data () {
    return {
      msg: '您还未注册，请先注册'
    }
  },
  methods: {
    registerClcik: function () {
      this.$router.push({
        path: `/register`
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.warnImgClass {
    margin-top: 70px;
    height: 60px;
    width: 60px;
}

.register {
    width: 76%;
    background-color: rgb(51, 185, 189);
    border: none;
    height: 40px;
    margin-top: 80px;
    border-radius: 3px;
    color: white;
    font-size: 1.1rem;
}
</style>



